/* projectList.css */
.project-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-image: url("../img/sfondo.png");
  background-position: top center;
  background-size: 100% 120%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  list-style: none;
  padding: 40px;
}

.project-item {
  width: calc(33.33% - 10px); /* Adjust the width as needed */
  margin-bottom: 20px;
  box-sizing: border-box;
}

.project-item img {
  max-width: 100%;
  height: auto;
  cursor: pointer;
}

.project-item h2 {
  font-size: 1.2rem;
  margin-top: 10px;
}

.project-item p {
  margin-top: 5px;
}

/* Added overlay for the fade-in effect */
.project-item .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(
    0,
    0,
    0,
    0.5
  ); /* Adjust the background color and opacity as needed */
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  pointer-events: none; /* Allow hovering through the overlay */
}

.project-item:hover img {
  opacity: 0.7; /* Adjust the opacity when hovering as desired */
}

.project-item:hover .overlay {
  opacity: 1;
}

@media screen and (max-width: 600px) {
  .project-list {
    background-size: cover; /* Adjust background size for small screens */
  }
  .project-item {
    width: 100%; /* Adjust the width as needed */
    margin-bottom: 20px;
    box-sizing: border-box;
  }
}
