.c {
  height: 100%;
  padding-bottom: 50px;
  position: relative;
  background-image: url("../img/sfondo.png");
  background-position: top center;
  background-size: 100% 120%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: black;
}

.c-wrapper {
  padding: 40px 0px;
  display: flex;
}

.c-left {
  flex: 1;
  padding: 20px;
}

.c-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  padding: 20px;
}

.c-title {
  font-size: 40px;
  width: 80%;
  padding: 0px;
}

.c-info-item {
  display: flex;
  align-items: center;
  margin: 50px 0px;
  font-weight: 300;
  width: 70%;
}

.c-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.c-desc {
  font-weight: 200;
  padding: 20px 0px;
}

form {
  margin-top: 20px;
}

input {
  width: 50%;
  height: 50px;
  border: none;
  border-bottom: 1px solid black;
  margin: 10px 0px;
  font-size: 15px;
  padding-left: 10px;
}

textarea {
  width: 100%;
  margin: 10px 0px;
  font-size: 15px;
  padding-left: 10px;
}

button {
  border: none;
  padding: 15px;
  color: white;
  background-color: black;
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .c {
    width: 100%;
    background-size: cover; /* Adjust background size for small screens */
  }
  .c-wrapper {
    flex-direction: column;
    padding: 0px 50px;
  }

  .c-title {
    font-size: 20px;
  }

  .c-info-item {
    margin: 20px 0px;
    width: 100%;
  }

  /* Hides specific parts only in mobile view */
  /* .c-desc {
    display: none;
  } */

  /*****/
  form {
    margin-top: 0px;
    display: flex;
    flex-wrap: wrap;
  }

  input {
    width: 35%;
    height: 40px;
    margin: 10px;
    margin-left: 0;
  }

  button {
    margin-top: 10px;
  }
}
