.a {
  height: 100%;
  padding-bottom: 50px;
  position: relative;
  background-image: url("../img/sfondoNero.png");
  background-position: top center;
  background-size: 100% 120%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: white;
}

.a-wrapper {
  padding: 0px;
  display: flex;
}

.a-left {
  flex: 1;
  padding: 0px 40px;
}

.a-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px;
  text-align: center;
}

.a-desc {
  font-weight: 200;
  padding: 20px 0px;
  text-align: center;
}

.a-info-item {
  display: flex;
  align-items: center;
  margin: 50px 0px;
  font-weight: 300;
  width: 70%;
  padding: 0px 50px;
}

.a-image {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.cv-link {
  color: black; /* Use the text color of the parent element (button) */
  text-decoration: none; /* Remove underlining */
}
/* Button style */
button {
  background-color: #73a848; /* Initial color */
  color: black;
  border: none;
  margin-top: 50px;
  padding: 10px;
  border-radius: 20px;
  cursor: pointer;
  max-width: 25%;
  width: 100%;
  transition: background-color 0.3s ease; /* Smooth transition on hover */
}

/* Hover effect */
button:hover {
  background-color: #1e4805; /* Lighter green on hover */
}

@media screen and (max-width: 600px) {
  .a {
    width: 100%;
    background-size: cover; /* Adjust background size for small screens */
  }

  .a-wrapper {
    flex-direction: column;
  }

  .a-left {
    width: 100%; /* Set the width to 100% for small screens */
    padding: 20px; /* Adjust padding for small screens */
  }

  .a-info-item {
    margin: 20px 0px; /* Adjust margin for small screens */
  }
  button {
    max-width: 50%;
  }
}
